import React, { useState , useEffect } from "react";
import * as Survey from "survey-react";
import "survey-react/modern.css";
import SurveyTemplate from './SurveyTemplate'



Survey.StylesManager.applyTheme("modern");


  
const useStyles = theme => ({
    root: {
      width: '100%',
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
      fontWeight: theme.typography.fontWeightRegular,
    },
  });

function SurveyComponent (props) {
    
   

    var json ="";
       
 
    // const food = new Survey.Model( SurveyTemplate.foodSurvey );
  
    useEffect(() => {
        
        var myHeaders = new Headers();
        var token = 'Bearer '+ sessionStorage.getItem('token')
        console.log(token)
        
        myHeaders.append("Authorization", token);
        myHeaders.append("Content-Type", "application/json");
  
        var requestOptions = {
            method: 'GET',
            redirect: 'follow',
            headers: myHeaders
          };
         
        let url  =  process.env.REACT_APP_API_SERVER_ADDRESS + "/Vipreceiptheaders?ReceiptCode=" + props.receiptCode 
          
        let allDone = false;
        console.log(url)
        
        fetch(url , requestOptions)
        .then(response => response.text())
        .then(result => {
            var jsonResult = JSON.parse(result)
            for(let a of jsonResult){             
               if(a.IsFeekbackFilled){
                   allDone = true
               }
               else{
                   allDone =false
                   break
                
               }
               for(let b of a.vipreceiptdetails){
                if(b.comment)
                    console.log(b.comment)
              }
            }
            })
        .catch(error => console.log('error', error));

        

        if (allDone){            

          
            var IsFeekbackFilled = '{  "IsFeekbackFilled": true}'                       
          
            var requestOptions = {
              method: 'PUT',
              headers: myHeaders,
              body: IsFeekbackFilled,
              redirect: 'follow'
            };
            
            
            let url  =   process.env.REACT_APP_API_SERVER_ADDRESS+"/Vipreceiptheaders/" +  props.headerid
            
         
            fetch(url , requestOptions)
              .then(response => response.text())
              .then(result => console.log(result))
              .catch(error => console.log('error', error));

        }

      
    }, [])

    var survey;
    // if(props.category=="飲品"){
    //     json = SurveyTemplate.drinkSurvey;
    //     survey = new Survey.Model(json);
      
    // }
    // else{
    json = SurveyTemplate.foodSurvey;
    survey = new Survey.Model(json);
        
    // }

        survey.onUploadFiles
        .add(function (survey, options) {
            console.log('trigger')
            var formData = new FormData();
            options
                .files
                .forEach(function (file) {
                    console.log(file)
                    formData.append('files', file);
                    formData.append('refId', props.id);
                    formData.append('ref', 'vipreceiptdetail');
                    formData.append('field', 'Photo');
                    console.log(props.id)
                    console.log(formData.get('files'))
                });
            var xhr = new XMLHttpRequest();

         
            xhr.open("POST",   process.env.REACT_APP_API_SERVER_ADDRESS + "/upload"); // https://surveyjs.io/api/MySurveys/uploadFiles
            xhr.setRequestHeader('Authorization', 'Bearer ' + sessionStorage.getItem('token'));
            xhr.onload = function () {
                console.log(xhr.responseText)

            };

            xhr.send(formData);
        })
        survey.onComplete
        .add(function (result,options) {
           

            let myHeaders = new Headers();
            let token = 'Bearer '+ sessionStorage.getItem('token')
            console.log(token)
            myHeaders.append("Content-Type", "application/json");
            myHeaders.append("Authorization", token);          
          
            var comment = '{  "Comment": ' + JSON.stringify(result.data) +  '}'      
            console.log(comment)

            var requestOptions = {
              method: 'PUT',
              headers: myHeaders,
              body: comment,
              redirect: 'follow'
            };            
            
            let url  =   process.env.REACT_APP_API_SERVER_ADDRESS+"/Vipreceiptdetails/" + props.id 
            console.log(url)
            

            //level1
            //not check overall yet
            fetch(url , requestOptions)
              .then(response => response.text())
              .then(result =>{
                  console.log(result)
            
                if(/*props.OverallFoodDone &&*/ props.OverallServiceDone){
                    let requestOptions = {
                        method: 'GET',
                        redirect: 'follow',
                        headers: myHeaders
                    };
                    
                    url  =   process.env.REACT_APP_API_SERVER_ADDRESS+"/Vipreceiptheaders?ReceiptCode=" + props.receiptCode 
                    
                    let allDone = false;
                    console.log(url)
                    //level2
                    fetch(url , requestOptions)
                    .then(response => response.text())
                    .then(result => {
                        console.log(result)
                        var jsonResult = JSON.parse(result)
                        for(let a of jsonResult){             
                            console.log(a)
                            for(let b of a.vipreceiptdetails){
                                console.log(b.Comment)
                                if(b.Comment){                   
                                    allDone = true
                                }else{
                                    allDone =false
                                    break
                                }
                            }
                        }

                        console.log(allDone)

                    if (allDone ){                  
                    
                        var IsFeekbackFilled = '{  "IsFeekbackFilled": true}'                       
                    
                        var requestOptions = {
                        method: 'PUT',
                        headers: myHeaders,
                        body: IsFeekbackFilled,
                        redirect: 'follow'
                        };
                        
                        
                        
                        url  =  process.env.REACT_APP_API_SERVER_ADDRESS+"/Vipreceiptheaders/" + props.headerid
                        //level3
                        fetch(url , requestOptions)
                        .then(response => response.text())
                        .then(result => console.log(result))
                        .catch(error => console.log('error', error));
                    }



                        })
                    .catch(error => console.log('error', error));
        
                    
                    
                }
            }).catch(error => console.log('error', error));

            props.parentCallback(props.index);
                
            


          


            // document
            //     .querySelector('#surveyResult')
            //     .textContent = "Result JSON:\n" + JSON.stringify(result.data, null, 3);
        });
  
    return (
        
        <div>                
                <Survey.Survey
                model={survey}
                    />               
            
        </div> );

    
    


    
}


export default SurveyComponent;
