import React,{useState,useEffect} from 'react';

import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { makeStyles,withStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';
import photo from '../toaWallpaper.jpg';
import './Menu.css';
import Box from '@material-ui/core/Box';


function Copyright() {


  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="">
        Taste Of Asia
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const Title = withStyles({
  root: {
    // color: "#ff0000"
  }
})(Typography);

const Notice = withStyles({
  root: {
    fontSize: "20px"
  }
})(Typography);

const useStyles = makeStyles((theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  heroContent: {
    //backgroundImage: 'url(' + photo + ')',
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(8, 0, 6),
    
   


  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    backgroundImage: "url('../toaWallpaper.JPG')",
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {

    paddingTop: '100%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
}));

const cards = [1, 2, 3, 4, 5, 6,7,8];


export default function Menu() {
  const classes = useStyles();
 

  const [Menu,setMenu] = useState([new Map()]);
  const [notice,setNotice] = useState('');
  useEffect(() => {  

    



    var myHeaders = new Headers();
    let token = 'Bearer '+ sessionStorage.getItem('token')
   
    
    myHeaders.append("Authorization", token);



    var requestOptions = {
      method: 'GET',
      headers: myHeaders,

      redirect: 'follow'
    };

    fetch( process.env.REACT_APP_API_SERVER_ADDRESS +"/menus?_sort=id:DESC", requestOptions )
      .then(response => response.text())
      .then(result =>{

        var json = JSON.parse(result);
   
        let tmpMenu = new Array();  
        for (var k in json){
            
         
            let map = new Map();
            map.set('content',json[k]['Content']);
            map.set('shop',json[k]['Name']);
            map.set('url', ( process.env.REACT_APP_API_SERVER_ADDRESS + json[k]['MenuPhoto']['url']));
            map.set('header',json[k]['header']);
            
            tmpMenu.push(map);
        }        
    
        setMenu(tmpMenu);
        var f = tmpMenu[2]
    
        })
      .catch(error => console.log('error', error));      


 
    fetch(  process.env.REACT_APP_API_SERVER_ADDRESS+"/notice" , requestOptions)
      .then(response => response.text())
      .then(result => {
        var json = JSON.parse(result);
        setNotice(json.notice)
      })
      .catch(error => console.log('error', error));   
  
    },[]);
 
  if(sessionStorage.getItem('token') == '' || sessionStorage.getItem('token') == null ){
    window.location.href =   process.env.REACT_APP_WEB_SERVER_ADDRESS+"/login";
  }

  
  return (
    <React.Fragment>
      
      <main>
        {/* Hero unit */}
        <div className={classes.heroContent}>
          <Container maxWidth="sm" >
            <Title component="h1" variant="h2" align="center" color="textPrimary" gutterBottom>
                Taste Of Asia
            </Title>
         
            <Paper elevation={3}  >
            <Notice className="new-line">
            {notice} </Notice>
            </Paper  >
            {/* <Typography variant="h5" align="center" color="textSecondary" paragraph>
              VIP Recommand Menu
            </Typography> */}
            {/* <div className={classes.heroButtons}>
              <Grid container spacing={2} justify="center">
                <Grid item>
                  <Button variant="contained" color="primary"  >
                    Main call to action
                  </Button>
                </Grid>
                <Grid item>
                  <Button variant="outlined" color="primary"onClick={() => { alert(Menu[0])}}>
                    Secondary action
                  </Button>
                </Grid>
              </Grid>
            </div> */}
          </Container>
        </div>
        <Container className={classes.cardGrid} >
          {/* End hero unit */}
          <Grid container spacing={8}>

            {Menu.map((menu) => (
              
              <Grid item key={menu.get('url')} xs={12} sm={4} md={4} >

                <Grid container  align="center"  justify="center"  direction="column">
                  <Box > 
                    <Typography component="h1" variant="h4" align="center" color="textPrimary" >{menu.get('header')}</Typography>
                  </Box>
                </Grid>
                
                <Card className={classes.card}>
                  
                  <CardMedia
                    className={classes.cardMedia}
                    image={menu.get('url')}
                    title={menu.get('shop')}
                  />
                  <CardContent className={classes.cardContent}>
                    <Typography gutterBottom variant="h5" component="h2">
                         {menu.get('shop')}
                    </Typography>
                    <Typography className='new-line'>
                      {menu.get('content')}
                    </Typography>
                  </CardContent>
                  {/* <CardActions>
                    <Button size="small" color="primary">
                      View
                    </Button>
                    <Button size="small" color="primary">
                      Edit
                    </Button>
                  </CardActions> */}
                </Card>
              </Grid>
            ))}
          </Grid>
        </Container>
      </main>
      {/* Footer */}
      <footer className={classes.footer}>
        <Typography variant="h6" align="center" gutterBottom>
          
        </Typography>
        <Typography variant="subtitle1" align="center" color="textSecondary" component="p">
          Michael Lin
        </Typography>
        <Copyright />
      </footer>
      {/* End footer */}
    </React.Fragment>
  );
}