import React,{useState,useEffect} from 'react';


import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { makeStyles,withStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';

import './Rule.css';




const Notice = withStyles({
  root: {
    fontSize: "20px"
  }
})(Typography);

const useStyles = makeStyles((theme) => ({
  icon: {
    marginRight: theme.spacing(2),
  },
  heroContent: {
    //backgroundImage: 'url(' + photo + ')',
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(2, 0, 6),
    
   


  },
  heroButtons: {
    marginTop: theme.spacing(4),
  },
  cardGrid: {
    paddingTop: theme.spacing(8),
    paddingBottom: theme.spacing(8),
  },
  card: {
    backgroundImage: "url('../toaWallpaper.JPG')",
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
  },
  cardMedia: {

    paddingTop: '100%', // 16:9
  },
  cardContent: {
    flexGrow: 1,
  },
  footer: {
    backgroundColor: theme.palette.background.paper,
    padding: theme.spacing(6),
  },
}));



export default function Rule() {
  const classes = useStyles(); 

  const [notice,setNotice] = useState('');
  useEffect(() => {   

    
    var myHeaders = new Headers();
    let token = 'Bearer '+ sessionStorage.getItem('token')
    console.log(token)
    
    myHeaders.append("Authorization", token);



    var requestOptions = {
      method: 'GET',
      headers: myHeaders,

      redirect: 'follow'
    };
 
    fetch(  process.env.REACT_APP_API_SERVER_ADDRESS+"/rule", requestOptions)
      .then(response => response.text())
      .then(result => {
        var json = JSON.parse(result);
        console.log(json)
        setNotice(json.Rule)
      
      })
      .catch(error => console.log('error', error));   
      
    },[]);
 
  if(sessionStorage.getItem('token') == '' || sessionStorage.getItem('token') == null ){
    window.location.href =   process.env.REACT_APP_WEB_SERVER_ADDRESS+"/login";
  }

  
  return (
    <React.Fragment>
      
      <main>
        {/* Hero unit */}
        <div className={classes.heroContent}>
      
            
            <Paper elevation={3}  >
            <Notice className="new-line">
            {notice} </Notice>
            </Paper  >
           
          
        </div>
       
      </main>
      {/* Footer */}
     
      {/* End footer */}
    </React.Fragment>
  );
}