import { CSVLink, CSVDownload } from "react-csv";
import {useState} from 'react' 


export default function Report(){

  var [csvData,setcsvData] = useState([])
  const [gen,SetGen] = useState(false)



  const handleClick = () =>{

    let myHeaders = new Headers();
    let token = 'Bearer '+ sessionStorage.getItem('token')
    console.log(token)
   
    myHeaders.append("Authorization", token);

    let requestOptions = {
      method: 'GET',
      headers: myHeaders,
      redirect: 'follow'
    };
    
    fetch("http://192.168.1.171:1337/vipreceiptheaders/genreport18?fromdate=2021-03-11&todate=2021-04-11", requestOptions)
      .then(response => response.text())
      .then(result =>{
        
        csvData = JSON.parse(result)
        console.log(csvData)
        setcsvData(csvData)
      
      })
      .catch(error => console.log('error', error));
    

    SetGen(true)

  }  
  return (
    <div>
    {
      gen ?  <CSVLink data={csvData}>Download me</CSVLink>:
      <button onClick={handleClick}>click me</button>     
    }  
    </div>
    )
}