import React,{useState,useEffect} from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { useHistory } from "react-router-dom";


function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="https://material-ui.com/">
        Taste Of Asia
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

function setToken(userToken:String, username:String) {
    sessionStorage.setItem('token', userToken);
    sessionStorage.setItem('username', username );    
  }

  
function getToken() {
    const tokenString = sessionStorage.getItem('token');
    // const userToken = JSON.parse(tokenString);

    return tokenString;
}



const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SignIn(props) {
  const classes = useStyles();
  const [username, setUserName] = useState('');
  const [password, setPassword] = useState('');
  const [correct,setCorrect] = useState(true);

  const history = useHistory();


//  useEffect(() => {
//    // 使用瀏覽器 API 更新文件標題
//        setToken('','');
//
//  });
  const login = ()=>{
      console.log(username,password);

  }

  const request_server_token = ()=>{
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");

    var raw = JSON.stringify({"identifier":username,"password":password});

    var requestOptions = {
    method: 'POST',
    headers: myHeaders,
    body: raw,
    // redirect: "follow" 
    };
   

    fetch( process.env.REACT_APP_API_SERVER_ADDRESS +"/auth/local", requestOptions)
    .then(response => response.text())
    .then(result => {console.log(result)
        if (result){
            const tmp = JSON.parse(result)
            const statusCode = tmp['statusCode'];
            console.log(statusCode)
             
            if(!statusCode)    { 
                setToken(tmp['jwt'],username)   
                
                props.parentCallBack() 
                // window.location.href = process.env.REACT_APP_WEB_SERVER_ADDRESS +"/menu";
                let path = '/toa_VIP/';
             
                history.push({
                  pathname: path                
                });
                // history.go(0)
            }
        }
        else{
            setCorrect(false);
        }
    })
    .catch(error => {       
     
        //setCorrect(false);
        console.log('error', error)       
    })
   

  }
  
  return (
         <Container component="main" maxWidth="xs">
         <CssBaseline />
         <div className={classes.paper}>
           <Avatar className={classes.avatar}>
                {/* <img  scr={companyLogo} width="400" height="400"/> */}
           </Avatar>
           <Typography component="h1" variant="h5">
               Login 
           </Typography>
           <form className={classes.form} noValidate>
             <TextField
               variant="outlined"
               margin="normal"
               required
               fullWidth
               id="email"
               label="VIP CODE"
               name="email"     
               autoComplete="email"
               autoFocus
               onChange={e => setUserName(e.target.value)}
             />
             <TextField
               variant="outlined"
               margin="normal"
               required
               fullWidth
               name="password"
               label="Password"
               type="password"
               id="password"
               autoComplete="current-password"
               onChange={e => setPassword(e.target.value)} 
             />
             {/* <FormControlLabel
               control={<Checkbox value="remember" color="primary" />}
               label="Remember me"
             /> */}
             <Button                             
               fullWidth
               variant="contained"
               color="primary"
               onClick={request_server_token}
               className={classes.submit}
             >
               Sign In
             </Button>

            
             {/* <Grid container>
               <Grid item xs>
                 <Link href="#" variant="body2">
                   Forgot password?
                 </Link>
               </Grid>
               <Grid item>
                 <Link href="#" variant="body2">
                   {"Don't have an account? Sign Up"}
                 </Link>
               </Grid>
             </Grid> */}
           </form>
           {/* { correct ? <div></div>:<Typography component="h2" variant="h5">
               Wrong
           </Typography>} */}
         </div>
         <Box mt={8}>
           <Copyright />
         </Box>
       </Container>
  );
}
