import './App.css';
import {useState} from 'react'
import Menu from './Components/NavigationBar/Menu/Menu';
import Login from './Components/Login/Login';
import AppBar from '@material-ui/core/AppBar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';

import { makeStyles } from '@material-ui/core/styles';
import Chat from './Components/Message/Message'
import Survey from './Components/NavigationBar/Surveys/Survey'
import {PrivateRoute} from "./Components/Login/PrivateRoute"
import Comment from './Components/CommentSection/CommentSection'
import SurveyTable from './Components/NavigationBar/Surveys/SurveyTable'
import FinishSurvey from './Components/NavigationBar/Surveys/FinishSurvey'
import AutoLogin from './Components/Login/AutoLogin'
import Report from './Components/Report/Report'
import Rule from './Components/NavigationBar/Rule/Rule'
import AdminTable from './Components/NavigationBar/Surveys/AdminTable'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  Redirect
} from "react-router-dom";


// function setToken(userToken:String) {
//   sessionStorage.setItem('token', JSON.stringify(userToken));  
// }

function getToken() {
  const tokenString = sessionStorage.getItem('token');
  // const userToken = JSON.parse(tokenString);

  return tokenString;
}

function getUsername(){
  return sessionStorage.getItem('username')

}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
    color: 'white',
  
  },
  title: {
    flexGrow: 1,
  },
  Button:{    
    color: 'white',
    
    
    
  },
  username:{
    marginRight: theme.spacing(2),

  },
  toolbarButtons: {
    marginLeft: 'auto',
  },
  toolbar: {

    alignItems: 'flex-start',
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
  },
}));





function App() {
  

  const classes = useStyles();
  const [isAdmin, setIsAdmin ] = useState(false)
  const [showLogout, setShowLogout ] = useState(false)

 

  const logout = ()=>{
    sessionStorage.clear();
    window.location.href =  process.env.REACT_APP_WEB_SERVER_ADDRESS+"/login";
  }


  const isAdminCallBack = ()=>{
    console.log(sessionStorage.getItem('username'))
    if (sessionStorage.getItem('username')=='hradmin')
      setIsAdmin(true)
    
      setShowLogout(true)

 

  }
  const showLogoutCallBack = () =>{
    setShowLogout(true)
  }
  console.log("not need commit")

  const windowUrl = window.location.search;
  console.log(windowUrl)
  const params = new URLSearchParams(windowUrl);
  const username = params.get('username')
  const ID = params.get('ID')
  var autologin;
  if (ID) 
      autologin = true
  else
      autologin = false
  console.log(autologin)
  // const SurveyRoute = <Survey/>;
   return (
    
    <Router basename={ process.env.BASE_URL }>   

      <CssBaseline /> 
      <div className={classes.root}>
      <AppBar  position='static'>
        <Toolbar className={classes.toolbar} style={{ background: '#2E3B55' }}>
          
          <Link to='/menu'  > 
            <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu" >
              <Typography>推薦餐牌</Typography>
            </IconButton>
          </Link>
          <Link to='/surveytable'  > 
            <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu" >
            <Typography> 問卷 </Typography>
            </IconButton>
          </Link>

          <Link to='/rule'  > 
            <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu" >
            <Typography> VIP守則 </Typography>
            </IconButton>
          </Link>
          {isAdmin ?
          <Link to='/admintable'  > 
            <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu" >
            <Typography> Admintable </Typography>
            </IconButton>
          </Link>: <div></div>}
        
        
          {/* <Typography variant="h6" className={classes.title}>
            VIP
          </Typography>     */}
         
        
         <div className={classes.title} variant="h5" noWrap>
            
        </div>
         <IconButton color="inherit" edge='end'>
         <Typography variant="h6"   color="inherit" className={classes.username}>
            {getUsername()}
          </Typography>     
          </IconButton>  

          <IconButton color="inherit"  onClick={logout} edge='end'>
          { showLogout &&<div color="primary" >Logout</div>}
          </IconButton>  
        </Toolbar>
      </AppBar> 
    
        <Switch>
        <Route exact path="/toa_VIP/" render={() => {
                    return ( 
                      autologin?
                    <Redirect to={{
                      pathname: "/autoLogin",
                      state: { username: username,id:ID } ,
                      parentCallBack : {isAdminCallBack}
                                         
                    }
                    } /> 
                    :                                         
                      <Redirect to="/menu" /> 
                    )
                }}></Route>
          <Route exact path="/login" render={(props) => <Login {...props} parentCallBack = {isAdminCallBack}/>} />
          <PrivateRoute exact path="/Report" component={Report}></PrivateRoute>
          <PrivateRoute exact path="/Rule" component={Rule}></PrivateRoute>
          <Route path="/autoLogin" component={AutoLogin} ></Route>
          <PrivateRoute exact path ="/survey" component={Survey}  />
          <PrivateRoute exact path ="/message" component={Chat}/>         
          <PrivateRoute exact path ="/menu" component={Menu}/>
          <PrivateRoute exact path ="/comment" component={Comment}/>
          <PrivateRoute exact path ="/SurveyTable" component={SurveyTable}/>
          <PrivateRoute exact path ="/FinishSurvey" component={FinishSurvey}/>
          <PrivateRoute exact path ="/Admintable" component={AdminTable}/>


        </Switch>
      {/* <Switch>
        <Route path="/menu">
          <Menu/>
        </Route>
        <Route path="/login">
          <Login/>
        </Route>
        <Route path="/message">
          <Chat></Chat>
        </Route>
        <Route path="/survey">
          <Survey/>
        </Route>
        { <PublicRoute restricted={true} component={SignIn} path="/signin" exact />
        <PrivateRoute component={Search} path="/search" exact />
        }
      </Switch>  */}
      </div>
    </Router> 
  );
}

export default App;
